import { useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faBars, faRectangleXmark } from '@fortawesome/free-solid-svg-icons';
import cls from '../Navbar.module.scss';
import { Link, useNavigate } from 'react-router-dom';
import { isMobile } from 'react-device-detect';
import { FLOWHERO } from '../../../../shared/utility';

export const Navbar = props => {
	const { background = 'white' } = props;
	const [nav, setNav] = useState(false);
	const navigate = useNavigate();


	
	const openNav = () => {
		setNav(!nav);
	};

	return (
		<div className={cls.navbarsection} style={{ position: 'fixed', backgroundColor: background }}>
			<div className={cls.navbartitle} style={{ fontSize: isMobile ? '20px' : '26px', marginLeft: isMobile ? '0' : '50px', width: '20%' }}>
				{FLOWHERO(isMobile ? '26px' : '28px', 'var(--app-color-gray-dark)', 'bold')}
			</div>
			{/* Desktop */}
			<ul className={cls.navbaritems} style={{ display: isMobile ? 'none' : '', width: '100%', justifyContent: 'right', padding: '0 20px' }}>
				<li>
					<Link to="/#/home" className={cls.navbarlinks} style={{ cursor: 'pointer', 
						fontWeight:'bold' 
						}}>
						{`HOME`}
					</Link>
				</li>
				<li>
					<a
						className={cls.navbarlinks}
						style={{ cursor: 'pointer', 
							fontWeight:'bold' 
						}}
						onClick={() => {
							window.scrollTo({ top: document.body.scrollHeight / 14, behavior: 'smooth' })
						}}
					>
						{'SOLUTIONS'}
					</a>
				</li>
				<li>
					<a
						className={cls.navbarlinks}
						style={{ cursor: 'pointer',
							 fontWeight:'bold'
							 }}
						onClick={() => {
							
							window.scrollTo({ top: document.body.scrollHeight / 5.2, behavior: 'smooth' })
						}}
					>
						{'USE CASES'}
					</a>
				</li>
				<li>
					<a
						className={cls.navbarlinks}
						style={{ cursor: 'pointer',
							 fontWeight:'bold'
							 }}
						onClick={() => {
							
							window.scrollTo({ top: document.body.scrollHeight / 1.26, behavior: 'smooth' })
						}}
					>
						{'LIBRARIES'}
					</a>
				</li>
				<li>
					<a
						className={cls.navbarlinks}
						style={{ cursor: 'pointer', 
							fontWeight:'bold'
						 }}
						onClick={() => {
							window.scrollTo({ top: document.body.scrollHeight / 1.14, behavior: 'smooth' })
						}}
					>
						{'PLANS'}
					</a>
				</li>
				
				<li>
					<a href="#/register" 
					className={cls.navbarlinks}
					style={{ cursor: 'pointer', 
						fontWeight:'bold'
					 }}
					>
						{`REGISTER`}
					</a>
				</li>
				<li>
					<a href="https://www.flowhero.app/#/login" 
					className={cls.navbarlinks}
					style={{ cursor: 'pointer', 
						fontWeight:'bold',
						color: 'green',
						border: '1px solid lightgray',
						padding: '10px'
					 }}
					>
						{`LOGIN`}
					</a>
				</li>
				<li style={{width: '20px'}}/>
			</ul>

			
			{/* Hamburger Icon */}
			<div className="mobile-nav" style={{ display: isMobile ? 'block' : 'none' }}>
				<FontAwesomeIcon icon={faBars} onClick={openNav} className="hamb-icon" />
			</div>
		</div>
	);
}

export default Navbar;
