import React, { useEffect, useState } from 'react';
import './HeroF.css';
import '../OurStack.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faAddressCard, faAngleUp, faCircleCheck, faStar } from '@fortawesome/free-solid-svg-icons';
import { useNavigate } from 'react-router-dom';
import ReactPlayer from 'react-player';
import { APP_NAME, PROMO_VIDEO } from '../../../../shared/utility';
import { isMobile } from 'react-device-detect';

function Hero() {
	const [goUp, setGoUp] = useState(false);

	const scrollToTop = () => {
		window.scrollTo({ top: 0, behavior: 'smooth' });
	};
	useEffect(() => {
		const onPageScroll = () => {
			if (window.scrollY > 600) {
				setGoUp(true);
			} else {
				setGoUp(false);
			}
		};
		window.addEventListener('scroll', onPageScroll);

		return () => {
			window.removeEventListener('scroll', onPageScroll);
		};
	}, []);

	return (

		<div className="flowhero-section-i" id="flowheroherohero" >
			<div style={{ display: 'flex', flexDirection: 'row', width: '100%', alignItems: 'center' }}>
				{!isMobile && <div className="about-image-content-i" style={{ width: '50%' }}>
					<div className="about-video-content" style={{ flexDirection: 'column' }}>
						<ReactPlayer width={'100%'} url={PROMO_VIDEO} />
					</div>
				</div>}

				<div className="text-section" style={{ padding: isMobile ? '' : '80px 80px 80px 20px' }}>
					<h2 className="text-title-ii">{`Workflow automation made simpler`}</h2>
					<p className="text-descritpion">{`We streamline your complex business processes through an intuitive, easy-to-use interface. Focus on what your business does best — we’ll handle the data collection and deliver the right information to your customers.`}</p>
				</div>

			</div>
			<Hero2 />
			<div onClick={scrollToTop} className={`scroll-up ${goUp ? 'show-scroll' : ''}`}>
				<FontAwesomeIcon icon={faAngleUp} />
			</div>
		</div>
	);
}

export default Hero;

